}

/* BREAKPOINT FOR TABLETS */
@media screen and (max-width: @smallBreakpoint) {
	.search-list {
		&__hit-title {
			&.-noThumbnail {
				padding-right: 0;
			}
		}
		&__hit-thumbnail {
			width: auto;
			justify-content: flex-start;
		}
		&__sorting {
			&__item {
